@import url(https://fonts.googleapis.com/css2?family=PT+Mono&family=PT+Sans:ital,wght@0,400;0,700;1,400;1,700&family=PT+Serif:ital,wght@0,400;0,700;1,400;1,700&display=swap);
body {
  margin: 0;
  font-family: 'PT Serif', serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.TextArea {
  --padding-size: 1ch;
  background: hsla(0, 52%, 96%, 0.4);
  position: relative;
}

.TextArea__input {
  z-index: 1;
  /*color: transparent;*/
  color: hsla(0, 0%, 0%, 0.5);
  caret-color: black;
  box-sizing: border-box;
  font-family: inherit;
  font-size: inherit;
  border: none;
  resize: none;

  outline: none;
  padding: var(--padding-size);
  margin: 0;
  white-space: inherit;
  background: transparent;

  position: absolute;
  top: 0;
  right: 0;
  min-height: 100%;
  width: 100%;
}

.TextArea__input-highlighter {
  font-size: inherit;
  position: relative;
  unicode-bidi: embed;
  white-space: inherit;
  padding: var(--padding-size);
  z-index: 0;
  overflow: auto;
}

.Word {
  cursor: pointer;
  margin-right: 1rem;
}
.Word--selected {
  background: hsla(51, 100%, 50%, 1);
}

.Word__count {
  font-size: 0.6em;
  font-family: 'PT Mono';
}

.Dictionary {
  padding: 1ch;
  line-height: 2em;
}

.Highlighter {
}

.Highlighter__item {
}

.Highlighter__item--active {
  background-color: hsla(51, 100%, 50%, 1);
}

.App {
  font-family: 'PT Serif', monospace;
  font-size: 1.3rem;

  height: 100vh;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  white-space: pre-wrap;
}

